// 
import { FC,useState } from "react"
import { Link } from "react-router-dom"
import { InboxOutlined } from '@ant-design/icons'
import type { UploadProps } from 'antd'
import { message, Upload } from 'antd'
import './index.css'
const { Dragger } = Upload

const Detection:FC = ()=>{
    const [check1,setCheck1] = useState(true)
    const [check2,setCheck2] = useState(false)
    const change1 = ()=>{
        setCheck1(true)
        setCheck2(false)
    }    
    const change2 = ()=>{
        setCheck1(false)
        setCheck2(true)
    }
    const [istrained,setIstrained] = useState(false)
    const props: UploadProps = {
        name: 'file',
        multiple: true,
        action: '/upload',
        onChange(info) {
          const { status } = info.file
          if (status !== 'uploading') {
            console.log(info.file, info.fileList)
            message.success(`!`)
            setIstrained(true)
          }
          if (status === 'done') {
            message.success(`!`)
            setIstrained(true)
          }
        },
        onDrop(e) {
          console.log('Dropped files', e.dataTransfer.files);
        },
        progress: {
            strokeColor: {
              '0%': '#108ee9',
              '100%': '#87d068',
            },
            strokeWidth: 3,
            format: (percent) => percent && `${parseFloat(percent.toFixed(2))}%`,
        }
    }
    const [istested,setIstested] = useState(false)
    const propss: UploadProps = {
        name: 'file',
        multiple: true,
        action: '/upload2',
        onChange(info) {
          const { status,response } = info.file
          if (status !== 'uploading') {
            console.log(info.file, info.fileList)
            message.success(`!`)
            setIstrained(true)
            setUrl(response.image_url)
          }
          if (status === 'done') {
            message.success(`${info.file.name} file uploaded successfully.`)
            // 
            message.success(`!`)
            setIstrained(true)
            setUrl(response.image_url)
          } 
        },
        onDrop(e) {
          console.log('Dropped files', e.dataTransfer.files);
        },
        progress: {
            strokeColor: {
              '0%': '#108ee9',
              '100%': '#87d068',
            },
            strokeWidth: 3,
            format: (percent) => percent && `${parseFloat(percent.toFixed(2))}%`,
        }
    }
    const [url,setUrl] = useState("")
    const downloadFile = ()=> {
        // GET
        // console.log(1)
        fetch('/download')
        .then(response => response.blob())
        .then(blob => {
            // URL
            const url = window.URL.createObjectURL(blob);
            
            // ahrefURL
            const link = document.createElement('a');
            link.href = url;
            
            // 
            link.download = 'model_param.pth';
            
            // a
            link.click();
            
            // URL
            window.URL.revokeObjectURL(url);
        })
    }
    const downloadFile2 = ()=> {
        // GET
        // console.log(1)
        fetch('/test1')
        .then(response => response.blob())
        .then(blob => {
            // URL
            const url = window.URL.createObjectURL(blob);
            
            // ahrefURL
            const link = document.createElement('a');
            link.href = url;
            
            // 
            link.download = 'model_param.pth';
            
            // a
            link.click();
            
            // URL
            window.URL.revokeObjectURL(url);
        })
    }
    const [src,setSrc] = useState("")
    const previewphoto = ()=>{
        console.log(11111)
        fetch('/tmp/test_src/.png')
            .then(response => response.blob())
            .then(blob => {
                // <img>src
                const img = document.getElementById('preview-image');
                setSrc(URL.createObjectURL(blob))
         })
    }
    return (
        <>
            <div className={"header active"} >
                <div className="container">
                    <nav className="navbar navbar-inverse" style={{display:'flex'}} role="navigation">
                        <div className="navbar-header" style={{marginLeft:'5%'}}>
                            <Link to='/' className="navbar-brand scroll-top"><em>F</em>aster</Link>
                        </div>
                        <ul className="nav navbar-nav" style={{display:'flex',marginLeft:'70%'}}>
                            <li >
                                <Link to='/' className="scroll-top">Home</Link>
                            </li>
                            <li >
                                <Link to='/Detection' className="scroll-top">Detection</Link>
                            </li>
                        </ul>
                    </nav>
                </div>
            </div>
            <div className="tabs-content" id="blog">
                    <div className="container">
                        <div className="row">
                            <div className="wrapper" style={{display:'flex'}}>
                                <div className="col-md-4" style={{width:'30%'}}>
                                    <div className="section-heading">
                                        <h4 style={{marginLeft:'5%'}}></h4>
                                        <div className="line-dec" style={{marginLeft:'5%'}}></div>
                                        <p style={{marginLeft:'5%'}}></p>
                                        <ul className="tabs clearfix" data-tabgroup="first-tab-group" style={{marginLeft:'5%'}}>
                                            <li><div onClick={()=>change1()} className={check1?"box active":"box"}></div></li>
                                            <li><div onClick={()=>change2()} className={check2?"box active":"box"}></div></li>
                                        </ul>
                                    </div>
                                </div>
                                <div className="col-md-8" style={{width:'60%',margin:'0 5%'}}>
                                    <section id="first-tab-group" className="tabgroup">
                                        <div id="tab1" style={check1?{display:''}:{display:'none'}}>
                                            <div className="text-content">
                                                <h4></h4>
                                                <span></span>
                                                <Dragger {...props}>
                                                    <p className="ant-upload-drag-icon">
                                                    <InboxOutlined />
                                                    </p>
                                                    <p className="ant-upload-text"></p>
                                                </Dragger>
                                                <div style={istrained?{display:'flex'}:{display:'none'}}>
                                                    <button className="download" onClick={downloadFile}></button>
                                                </div>
                                            </div>
                                        </div>
                                        <div id="tab2" style={check2?{display:''}:{display:'none'}}>
                                            <div className="text-content">
                                                <h4></h4>
                                                <span></span>
                                                <Dragger {...propss}>
                                                    <p className="ant-upload-drag-icon">
                                                    <InboxOutlined />
                                                    </p>
                                                    <p className="ant-upload-text"></p>
                                                </Dragger>
                                                <div style={istested?{display:'flex'}:{display:'none'}}>
                                                    <button className="download" onClick={downloadFile2}></button>
                                                    <button className="download" onClick={previewphoto}></button>
                                                </div>
                                                <div>
                                                    <img className={src === ""?"hidephoto":"showphoto"} src={src} alt="Preview Image" />
                                                </div>
                                            </div>
                                        </div>
                                    </section>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
        </>
    )
}
export default Detection